import { Maintenance_disposisi } from 'src/app/sdk/asset/models/maintenance-_disposisi';
import { AssetMaintenanceDisposisiService } from 'src/app/sdk/asset/services';
import { Component, OnInit, Input } from '@angular/core';
import { NzDrawerRef } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
    selector: 'app-add-disposisi',
    templateUrl: './add-disposisi.component.html',
    styleUrls: ['./add-disposisi.component.scss']
})
export class AddDisposisiComponent implements OnInit {

    @Input() value = '';

    ngOnInit(): void {

    }

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private assetMaintenanceDisposisiService: AssetMaintenanceDisposisiService
    ) { }

    close(): void {
        this.drawerRef.close(this.value);
    }

    disposisi: Maintenance_disposisi = {}
    addMaintenanceDisposisi() {

        this.spinner.show();
        this.assetMaintenanceDisposisiService.postAssetMaintenanceDisposisis(this.disposisi).subscribe(
            data => {
                this.spinner.hide()
                this.close()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Maintenance Code sudah ada');
            }
        )
    }

}
