import { SearhAssetComponent } from './../searh-asset/searh-asset.component';
import { AssetMaintenanceCodeService } from 'src/app/sdk/asset/services/asset-maintenance-code.service';
import { Maintenance_disposisi } from 'src/app/sdk/asset/models/maintenance-_disposisi';
import { Maintenance } from 'src/app/sdk/asset/models/maintenance';
import { Component, OnInit } from '@angular/core';
import { AssetMaintenanceService, AssetMaintenanceDisposisiService, AssetAssetService, VendorVendorService, AssetPartIdService, AssetLifeLimitService, AssetModelxService, AssetBinLocationService, VendorManufactureService, AssetConditionMonitoringService } from 'src/app/sdk/asset/services';
import { Vendor, Maintenance_code, Modelx, Part_id, Manufacture, Bin_location, Life_limit, Condition_monitoring } from 'src/app/sdk/asset/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { AddDisposisiComponent } from '../add-disposisi/add-disposisi.component';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private assetMaintenanceService: AssetMaintenanceService,
        private assetMaintenanceDisposisiService: AssetMaintenanceDisposisiService,
        private vendorVendorService: VendorVendorService,
        private assetAssetService: AssetAssetService,
        private assetMaintenanceCodeService: AssetMaintenanceCodeService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private drawerService: NzDrawerService,
        private assetPartIdService: AssetPartIdService,
        private assetLifeLimitService: AssetLifeLimitService,
        private assetModelxService: AssetModelxService,
        private assetBinLocationService: AssetBinLocationService,
        private vendorManufactureService: VendorManufactureService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,
    ) { }

    dataMaintenance: Maintenance = {}

    listDisposisi: Maintenance_disposisi[] = []
    listVendor: Vendor[] = []
    listMaitenannceCode: Maintenance_code[] = []
    dataModel: Modelx = {}
    dataType: Part_id = {}
    dataVendor: Vendor = {}
    dataManufacture: Manufacture = {}
    dataBin: Bin_location = {}
    dataLifelimit: Life_limit = {};
    dataCondition: Condition_monitoring = {};

    ngOnInit() {
        this.getPreeData()
        this.form()

    }
    getPreeData() {
        this.assetMaintenanceDisposisiService.getAssetMaintenanceDisposisis().subscribe(data => {
            this.listDisposisi = data.data
        })

        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })
        this.assetMaintenanceCodeService.getAssetMaintenanceCodes().subscribe(data => {
            this.listMaitenannceCode = data.data
        })
    }

    openCode(): void {
        const drawerRef = this.drawerService.create<AddDisposisiComponent, { value: string }, string>({
            nzTitle: 'Add new Maintenance disposisi',
            nzContent: AddDisposisiComponent,
            nzWidth: '320px'
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.getPreeData()
            }
        });
    }



    dataForm: FormGroupTypeSafe<Maintenance>
    form() {
        this.dataForm = this.fb.group<Maintenance>({
            id_vendor: new FormControl(''),
            id_maintenance_disposisi: new FormControl(''),
            tanggal_end_maintenance: new FormControl(''),
            tanggal_start_maintenance: new FormControl(''),
            id_asset: new FormControl(''),
            ref_maintenance_code: new FormControl(''),
            cost: new FormControl(''),
            is_waranty: new FormControl(''),
            title_maintenance: new FormControl(''),
            note_maintenance: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    id_asset: string = ''
    selectAsset() {
        const drawerRef = this.drawerService.create<SearhAssetComponent, { value: string }, string>({
            nzTitle: 'Search Asset',
            nzContent: SearhAssetComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_asset = data;
                this.dataForm.patchValue({ id_asset: this.id_asset })
                this.beforeAsset()
            }
        });
    }



    dataItem: any = {};

    beforeAsset() {
        console.log("panggie ko")
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data => {
                this.dataItem.sn = data.data.sn;
                this.dataItem.id_model = data.data.id_model;
                this.dataItem.purchase_date = data.data.purchase_date;
                this.dataItem.purchase_number = data.data.purchase_number;
                this.dataItem.price = data.data.price;
                this.dataItem.id_bin_location = data.data.id_bin_location;
                this.dataItem.id_vendor = data.data.id_vendor;
                this.getModel(this.dataItem.id_model)
                this.getBin(this.dataItem.id_bin_location)
                this.getVendor(this.dataItem.id_vendor)
            }
        )
    }

    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.dataModel = data.data
                this.getPartId(this.dataModel.id_part_id)
                this.getConditionMonitoring(this.dataModel.id_condition_monitoring)
                this.getLifeLimit(this.dataModel.id_life_limit)
                this.getManufacture(this.dataModel.id_manufacture)
            }
        )
    }
    getPartId(id_part_id) {
        this.assetPartIdService.getAssetPartIdsId(id_part_id).subscribe(
            data => {
                this.dataType = data.data
            }
        )
    }
    getLifeLimit(id_life_limit) {
        this.assetLifeLimitService.getAssetLifeLimitsId(id_life_limit).subscribe(
            data => {
                this.dataLifelimit = data.data
            }
        )
    }
    getConditionMonitoring(id_condition_monitoring) {
        this.assetConditionMonitoringService.getAssetConditionMonitoringsId(id_condition_monitoring).subscribe(
            data => {
                this.dataCondition = data.data
            }
        )
    }
    getManufacture(id_manufacture) {
        this.vendorManufactureService.getVendorManufacturesId(id_manufacture).subscribe(
            data => {
                this.dataManufacture = data.data
            }
        )
    }

    getBin(id_bin_location) {
        this.assetBinLocationService.getAssetBinLocationsId(id_bin_location).subscribe(
            data => {
                this.dataBin = data.data
            }
        )
    }

    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data
            }
        )
    }


    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataMaintenance = this.dataForm.value
        this.assetMaintenanceService.postAssetMaintenances(this.dataMaintenance).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/maintenance/repair/list'])
    }


}
