import { NgxSpinnerService } from 'ngx-spinner';
import { Maintenance } from 'src/app/sdk/asset/models/maintenance';
import { Component, OnInit } from '@angular/core';
import { AssetMaintenanceService, AssetMaintenanceCodeService, AssetMaintenanceDisposisiService, AssetPartIdService, AssetAssetService, AssetLifeLimitService, VendorVendorService, AssetModelxService, AssetBinLocationService, AssetStatusService, VendorManufactureService, AssetConditionMonitoringService } from 'src/app/sdk/asset/services';
import { AssetDetailService } from '../../../asset-detail/asset-detail.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Asset_archived, Modelx, Status, Asset, Part_id, Vendor, Manufacture, Bin_location, Life_limit, Condition_monitoring, Maintenance_disposisi, Maintenance_code } from 'src/app/sdk/asset/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private assetMaintenanceService: AssetMaintenanceService,
        private assetMaintenanceDisposisiService: AssetMaintenanceDisposisiService,
        private assetMaintenanceCodeService: AssetMaintenanceCodeService,
        private assetPartIdService: AssetPartIdService,
        private assetAssetService: AssetAssetService,
        private assetLifeLimitService: AssetLifeLimitService,
        private vendorVendorService: VendorVendorService,
        private assetModelxService: AssetModelxService,
        private assetBinLocationService: AssetBinLocationService,
        private assetStatusService: AssetStatusService,
        private vendorManufactureService: VendorManufactureService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private spinner: NgxSpinnerService,
    ) { }

    id_maintenance: string = ''
    dataMaintenance: Maintenance = {}
    dataModel: Modelx = {}
    dataDisposisi: Maintenance_disposisi = {}
    dataCode: Maintenance_code = {}
    dataAsset: Asset = {}
    dataType: Part_id = {}
    dataVendor: Vendor = {}
    dataManufacture: Manufacture = {}
    dataBin: Bin_location = {}
    dataLifelimit: Life_limit = {};
    dataCondition: Condition_monitoring = {};


    ngOnInit() {
        this.id_maintenance = this.activatedRoute.snapshot.paramMap.get('id_maintenance')
        this.getMaintenance()
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getMaintenance()
                    this.getLog()
                }
            }
        )


    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getMaintenance() {
        this.spinner.show()
        this.assetMaintenanceService.getAssetMaintenancesId(this.id_maintenance).subscribe(
            data => {
                this.dataMaintenance = data.data;
                this.getAsset(this.dataMaintenance.id_asset)
                this.getCode(this.dataMaintenance.ref_maintenance_code)
                this.getDisposisi(this.dataMaintenance.id_maintenance_disposisi)
                this.getVendor(this.dataMaintenance.id_vendor)

                this.params = {
                    id: data.data.id_maintenance,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_maintenance"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()

            }
        )
    }

    getCode(ref_maintenance_code) {
        this.assetMaintenanceCodeService.getAssetMaintenanceCodesId(ref_maintenance_code).subscribe(
            data => {
                this.dataCode = data.data
            }
        )
    }

    getDisposisi(id_maintenance_disposisi) {
        this.assetMaintenanceDisposisiService.getAssetMaintenanceDisposisisId(id_maintenance_disposisi).subscribe(
            data => {
                this.dataDisposisi = data.data
            }
        )
    }
    getAsset(id_asset) {
        this.assetAssetService.getAssetAssetsId(id_asset).subscribe(
            data => {
                this.dataAsset = data.data;
                this.getModel(this.dataAsset.id_model)
                this.getVendor(this.dataAsset.id_vendor)
                this.getModel(this.dataAsset.id_model)

            }
        )
    }


    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.dataModel = data.data
                this.getPartId(this.dataModel.id_part_id)
                this.getConditionMonitoring(this.dataModel.id_condition_monitoring)
                this.getLifeLimit(this.dataModel.id_life_limit)
                this.getManufacture(this.dataModel.id_manufacture)
            }
        )
    }
    getPartId(id_part_id) {
        this.assetPartIdService.getAssetPartIdsId(id_part_id).subscribe(
            data => {
                this.dataType = data.data
            }
        )
    }
    getLifeLimit(id_life_limit) {
        this.assetLifeLimitService.getAssetLifeLimitsId(id_life_limit).subscribe(
            data => {
                this.dataLifelimit = data.data
            }
        )
    }
    getConditionMonitoring(id_condition_monitoring) {
        this.assetConditionMonitoringService.getAssetConditionMonitoringsId(id_condition_monitoring).subscribe(
            data => {
                this.dataCondition = data.data
            }
        )
    }
    getManufacture(id_manufacture) {
        this.vendorManufactureService.getVendorManufacturesId(id_manufacture).subscribe(
            data => {
                this.dataManufacture = data.data
            }
        )
    }

    getBin(id_bin_location) {
        this.assetBinLocationService.getAssetBinLocationsId(id_bin_location).subscribe(
            data => {
                this.dataBin = data.data
            }
        )
    }

    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_maintenance })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }


    goToBack() {
        this.router.navigate(['/asset/maintenance/repair/list'])
    }


}
