import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RepairRoutingModule } from './repair-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { SearhAssetComponent } from './searh-asset/searh-asset.component';
import { AddDisposisiComponent } from './add-disposisi/add-disposisi.component';
import { DetailComponent } from './detail/detail.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { ApprovalModule } from 'src/app/components/approval/approval.module';


@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent, SearhAssetComponent, AddDisposisiComponent, DetailComponent],
    imports: [
        CommonModule,
        RepairRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        ApprovalModule
    ],
    providers: [FormBuilderTypeSafe],
    entryComponents: [
        SearhAssetComponent, AddDisposisiComponent
    ]

})
export class RepairModule { }
